<template>
  <v-main>
    <v-card class="calculator" color="">
      <input class="display" v-model="current">
      <v-btn class="btn dark" @click="clear" fab outlined dark>C</v-btn> 
      <v-btn class="btn dark" @click="sign" fab outlined dark>+/-</v-btn>
      <v-btn class="btn dark" @click="percent" fab outlined dark>%</v-btn>
      <v-btn class="btn operator" @click="divide" fab outlined dark>/</v-btn>
      <v-btn class="btn" @click="append('7')" fab outlined dark>7</v-btn>
      <v-btn class="btn" @click="append('8')" fab outlined dark>8</v-btn>
      <v-btn class="btn" @click="append('9')" fab outlined dark>9</v-btn>
      <v-btn class="btn operator" @click="multiply" fab outlined dark>x</v-btn>
      <v-btn class="btn" @click="append('4')"  fab outlined dark>4</v-btn>
      <v-btn class="btn" @click="append('5')"  fab outlined dark>5</v-btn>
      <v-btn class="btn" @click="append('6')"  fab outlined dark>6</v-btn>
      <v-btn class="btn operator" @click="minus"  fab outlined dark>-</v-btn>
      <v-btn class="btn" @click="append('1')"  fab outlined dark>1</v-btn>
      <v-btn class="btn" @click="append('2')"  fab outlined dark>2</v-btn>
      <v-btn class="btn" @click="append('3')"  fab outlined dark>3</v-btn>
      <v-btn class="btn operator" @click="plus"  fab outlined dark>+</v-btn>
      <v-btn class="btn zero" @click="append('0')"  fab outlined dark>0</v-btn>
      <v-btn class="btn" @click="dot"  fab outlined dark>.</v-btn>
      <v-btn class="btn operator" @click="equal"  fab outlined dark>=</v-btn>
    </v-card>
    <v-row>
      <v-col>
        <v-btn 
          width="30%"
          class="mt-6"
          @click="$router.go(-1)"
          color="blue"
          dark
        >
          返回
        </v-btn>
      </v-col>
    </v-row>

  </v-main>
</template>

<script>
export default {
  name: "CAL",
  data() {
    return {
      current: "",
      previous: "",
      operator: null,
      operatorClicked: false
    };
  },
  watch: {
    current: {
      immediate: true,
      handler() {
        const display = document.querySelector(".display");
        if (this.current.length > 10) {
          this.current = this.current.slice(0, 10);
        }
      }
    }
  },
  methods: {
    sign() {
      this.current =
        this.current.charAt(0) === "-"
          ? this.current.slice(1)
          : `-${this.current}`;
    },
    clear() {
      this.current = "";
      this.operator = null;
      this.operatorClicked = false;
    },
    percent() {
      this.current = `${parseFloat(this.current) / 100}`;
    },
    append(number) {
      if (this.operatorClicked) this.current = "";
      this.operatorClicked = false;
      this.current =
        this.current === "0" ? `${number}` : `${this.current}${number}`;
    },
    setPrevious() {
      this.previous = this.current;
      this.operatorClicked = true;
    },
    plus() {
      this.operator = (a, b) => a + b;
      this.setPrevious();
    },
    equal() {
      console.log(this.current, this.previous);
      this.current = `${this.operator(
        parseFloat(this.previous),
        parseFloat(this.current)
      )}`;
      this.operator = null;
      this.operatorClicked = false;
    },
    minus() {
      this.operator = (a, b) => a - b;
      this.setPrevious();
    },
    multiply() {
      this.operator = (a, b) => a * b;
      this.setPrevious();
    },
    divide() {
      this.operator = (a, b) => a / b;
      this.setPrevious();
    },
    dot() {
      if (this.current.indexOf(".") === -1) {
        this.append(".");
      }
    }
  }
};
</script>

<style>  
#app {
  text-align: center;
  margin-top: 60px;
}  
  
.flexbox {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
}
  
.calculator {
  display: grid;
  width: 400px;
  margin: 0 auto;
  font-size: 40px;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: minmax(50px, auto);
  border: .3px solid #868383;
  border-radius: 3rem;
  padding: .8em;
  background-color: #222;
}
  
.display {
  grid-column: 1/5;
  background-color: lightblue;
  text-align: right;
  padding-right: 20px;
  background-color: #D3D3D3;
  color: #333;
  margin-bottom: 10px;
  border-radius: 16px;
  outline: 0;
  font-size: 36px;
}
  
.btn {
  border: 1px solid black;
  background-color:black
}
  
.btn:hover {
 background-color: #868383;   
}
.dark {
  background-color: #333;
  color: #ffffff;
}
.operator {
  background-color: #e08d1f;
}
.zero {
  grid-column: 1/3;  
}
</style>